.sidebarRow {
  display: flex;
  margin-top: 10px !important;
  padding:5px ;
  align-items:center;
  align-self: baseline;
  border-radius: 15px;
}
.sidebarRow > .sidebarRow_Icon {
  color: gray;
}
.sidebarRow:hover > .sidebarRow_Icon {
  color: red;
  cursor: pointer;
}
.sidebarRow:hover {
  background-color: gainsboro;
  font-weight: 900;
}
.sidebarRow.selected {
  background-color: gainsboro;
  font-weight: 900;
}
.sidebarRow.selected > .sidebarRow_Icon {
  color: red;
}
.sidebarRow_Icon {
  margin: auto 10px;
}
.sidebarRow_title {
  flex: 1;
  font-weight: lighter;
  font-family: 'Comfortaa', cursive;
  font-size: 14px;
  padding-right: 2px;
  margin-bottom: 0%;
}
@media screen and (max-width: 700px) {
    h3
    {
       display: none;
    }
    .sidebarRow
    {
        width: fit-content;
        margin: 15px auto;
    }
    
}