.side_page
{
    flex: 0.2;
    padding-left: 15px;
    padding-right: 10px;

}
hr
{
    height: 1px;
    border: none;
    background-color: gray;
    margin-top: 10px;   
    margin-bottom: 20px;   
}

@media screen and (max-width: 600px) {
    .side_page
    {
       display: none;
    }

}
@media screen and (max-width: 700px) {
    .icons
    {
        width:fit-content;
    }
    hr
    {
       display: none;
    }
    .side_page
    {
       flex:0.08 ;
    }
}
.open
{
    display: block;
}