.channelRow{
    display: flex;
    width: 85%;
    align-items: center;
}
.channelRow_logo
{
    width: 120px !important;
    height: 120px !important;
    border-radius: 50%;
    margin: 10px 60px;
}
.channelRow_text
{
    display: flex;
    flex-direction: column;
}
.channelRow_text > h4
{
    display: flex;
    align-items: center;

}
.channelRow_text > p{
    font-size: small !important;
    color: gray;
}
